.tur-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 450px;
  padding: 15px 20px;
  border-top: 1px solid #eeeeee;
  margin: 0 auto;
  width: 100%;
  background-color: #ffffff;
  z-index: 99;
}

.tur-footer--nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.tur-footer--nav a {
  text-align: center;
  display: block;
  color: #aaaaaa;
  font-size: 16px;
}

.tur-footer--nav a:hover,
.tur-footer--nav a.active {
  color: #0869ff;
}

.tur-footer--nav a span {
  display: block;
  font-size: 12px;
  padding-top: 4px;
}